//统计接口1
    dianyingxinxiChat1() {
      this.$nextTick(()=>{

        this.$http({
            url: "group/dianyingxinxi/zhuyan",
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
                    xAxis.push(res[i].zhuyan);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].zhuyan
                    })
                }
                var option = {};
                this.wordclouds(pArray,'dianyingxinxiChart1');
				

            }
        });
      })
    },

//统计接口2
    dianyingxinxiChat2() {
      this.$nextTick(()=>{

        var dianyingxinxiChart2 = echarts.init(document.getElementById("dianyingxinxiChart2"),'macarons');
        this.$http({
            url: "dianyingxinxi/group/diqu",
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
					if(this.boardBase&&i==this.boardBase.pieNum){
						break;
					}
                    xAxis.push(res[i].diqu);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].diqu
                    })
                }
                var option = {};
				let titleObj = this.pie2?this.pie2.title:this.pie.title
				titleObj.text = '地区占比'
				
				const legendObj = this.pie2?this.pie2.legend:this.pie.legend
				
				let seriesObj = {
					type: 'pie',
					radius: ['25%', '55%'],
					center: ['50%', '60%'],
					data: pArray,
					emphasis: {
						itemStyle: {
							shadowBlur: 10,
							shadowOffsetX: 0,
							shadowColor: 'rgba(0, 0, 0, 0.5)'
						}
					}
				}
				seriesObj = Object.assign(seriesObj , this.pie2?this.pie2.series:this.pie.series)
				const gridObj = this.pie2.grid
				let tooltipObj = {
					trigger: 'item',
					formatter: '{b} : {c} ({d}%)'
				}
				tooltipObj = Object.assign(tooltipObj , this.pie2.tooltip?this.pie2.tooltip:{})
                option = {
					backgroundColor: this.pie2?this.pie2.backgroundColor:this.pie.backgroundColor,
					color: this.pie2?this.pie2.color:this.pie.color,
					title: titleObj,
					legend: legendObj,
					tooltip: tooltipObj,
					grid: gridObj,
					series: [seriesObj]
                };
                // 使用刚指定的配置项和数据显示图表。
                dianyingxinxiChart2.setOption(option);
				

                  //根据窗口的大小变动图表
				window.addEventListener('resize', () => {
                    dianyingxinxiChart2.resize();
                },false);
            }
        });
      })
    },

