//统计接口4
    dianyingxinxiChat4() {
      this.$nextTick(()=>{

        var dianyingxinxiChart4 = echarts.init(document.getElementById("dianyingxinxiChart4"),'macarons');
        this.$http({
            url: "dianyingxinxi/group/leixing",
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
					if(this.boardBase&&i==this.boardBase.pieNum){
						break;
					}
                    xAxis.push(res[i].leixing);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].leixing
                    })
                }
                var option = {};
				let titleObj = this.pie.title
				titleObj.text = '类型占比'
				
				const legendObj = this.pie.legend
				
				let seriesObj = {
					type: 'pie',
					radius: '55%',
					center: ['50%', '60%'],
					data: pArray,
					emphasis: {
						itemStyle: {
							shadowBlur: 10,
							shadowOffsetX: 0,
							shadowColor: 'rgba(0, 0, 0, 0.5)'
						}
					}
				}
				seriesObj = Object.assign(seriesObj , this.pie.series)
				const gridObj = this.pie.grid
				let tooltipObj = {
					trigger: 'item',
					formatter: '{b} : {c} ({d}%)'
				}
				tooltipObj = Object.assign(tooltipObj , this.pie.tooltip?this.pie.tooltip:{})
                option = {
					backgroundColor: this.pie.backgroundColor,
					color: this.pie.color,
					title: titleObj,
					legend: legendObj,
					tooltip: tooltipObj,
					grid: gridObj,
					series: [seriesObj]
                };
                // 使用刚指定的配置项和数据显示图表。
                dianyingxinxiChart4.setOption(option);
				

                  //根据窗口的大小变动图表
				window.addEventListener('resize', () => {
                    dianyingxinxiChart4.resize();
				},false);
            }
        });
      })
    },

//统计接口5
    dianyingxinxiChat5() {
      this.$nextTick(()=>{

        var dianyingxinxiChart5 = echarts.init(document.getElementById("dianyingxinxiChart5"),'macarons');
        this.$http({
            url: `dianyingxinxi/value/moviename/score`,
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
					if(this.boardBase&&i==this.boardBase.barNum){
						break;
					}
                    xAxis.push(res[i].moviename);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].moviename
                    })
                }
                var option = {};
				let titleObj = this.bar.title
				titleObj.text = '评分统计'
				
				const legendObj = this.bar.legend
				
				let xAxisObj = this.bar.xAxis
				xAxisObj.type = 'category'
				xAxisObj.data = xAxis
                
				
				let yAxisObj = this.bar.yAxis
				yAxisObj.type = 'value'
				
				let seriesObj = {
                        data: yAxis,
                        type: 'bar'
                    }
				seriesObj = Object.assign(seriesObj , this.bar.series)
				const gridObj = this.bar.grid
				let tooltipObj = {
					trigger: 'item',
					formatter: '{b} : {c}'
				}
				tooltipObj = Object.assign(tooltipObj , this.bar.tooltip?this.bar.tooltip:{})
                option = {
					backgroundColor: this.bar.backgroundColor,
					color: this.bar.color,
                    title: titleObj,
					legend: legendObj,
                    tooltip: tooltipObj,
					grid: gridObj,
                    xAxis: xAxisObj,
                    yAxis: yAxisObj,
                    series: [seriesObj]
                };
                // 使用刚指定的配置项和数据显示图表。
                dianyingxinxiChart5.setOption(option);


                  //根据窗口的大小变动图表
				window.addEventListener('resize', () => {
                    dianyingxinxiChart5.resize();
					},false);
            }
        });
      })
    },








































  }
};
